body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  font-size: 20pt;
  color: orange;
  height: 100vh;
  max-width: 100vw;
}
.landing-page {
	height: 100vh;
	max-width: 100%;
	display: flex;
	flex: 0 1 auto;
	flex-direction: column;
	justify-content: center;
}
nav {
	position:fixed;
	display: flex;
	flex: 1 0 auto;
	flex-direction: row;
	justify-content: space-between;
	top: 0;
	height: 3rem;
	width: 100vw;
	background-color: orange;
	box-shadow: 0px .1rem 8px 10px #89f1ff;
}

.title-wrapper {
	color: white;
	text-align: left;
	margin: .5rem 1rem;
	font-size: 2rem;
}

.header-title {
	margin: 0 auto;
	overflow: hidden;
	font-family: courier, monospace;
	text-align: left;
	white-space: nowrap;
	-webkit-animation: typing 2s steps(30, end);
	        animation: typing 2s steps(30, end);
}

.carot {
	-webkit-animation: blink 1s infinite;
	        animation: blink 1s infinite;
}

.menu {
	display: flex;
	flex: 1 0 auto;
	flex-direction: row;
	justify-content: flex-end;
	height: 100%;
}
.menu-item {
	margin: 1rem 1rem 1rem 0;
	font-size: 1.5rem;
	opacity: 1;
	color: white;
}

@-webkit-keyframes typing {
	from {width: 0;}
	to { width: 100%;}
}

@keyframes typing {
	from {width: 0;}
	to { width: 100%;}
}

@-webkit-keyframes blink {
	to { opacity: 0; }
}

@keyframes blink {
	to { opacity: 0; }
}
.content-grid {
	height: calc(100% - 3rem);
	width: calc(100% - 16rem);
	max-width: 100vw;
	display: flex;
	flex: 1 1 auto;
	justify-content: center;
	align-items: center;
	padding: 3rem 8rem 0 8rem;
}

.intro-wrapper {
	width: 100%;
	display: flex;
	flex: 0 1 auto;
	align-items: center;
	flex-direction: column;
}

.intro {
	display: flex;
	flex: 0 1 auto;
	align-items: center;
	justify-content: center;
	font-size: 4rem;
	padding-bottom: 2rem;
}

.description {
	display: flex;
	flex: 0 1 auto;
	align-items: center;
	justify-content: center;
	padding-bottom: 2rem;
}
.social-icon-bar {
	display: flex;
	flex: 0 1 auto;
	flex-direction: row;
	justify-content: center;
	vertical-align: middle;
	height: 3rem;
	width: auto;
}

.social-icon {
	display: flex;
	flex: 0 1 auto;
	vertical-align: middle;
	height: 100%;
	width: auto;
	padding: 0 1rem 0 1rem;
}

.social-icon:hover {
	-webkit-animation-name: hover-bounce;
	        animation-name: hover-bounce;
	-webkit-animation-duration: .5s;
	        animation-duration: .5s;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
	-webkit-animation-direction: alternate;
	        animation-direction: alternate;
	-webkit-animation-timing-function: ease;
	        animation-timing-function: ease;
}

@-webkit-keyframes hover-bounce {
	0% {-webkit-transform: translateY(0);transform: translateY(0)}
	100% {-webkit-transform: translateY(-25%);transform: translateY(-25%)}
}

@keyframes hover-bounce {
	0% {-webkit-transform: translateY(0);transform: translateY(0)}
	100% {-webkit-transform: translateY(-25%);transform: translateY(-25%)}
}
.control-panel {
	position: fixed;
	bottom: 0;
	width: 100vw;
	height: 2rem;
	display: flex;
	flex: 0 1 auto;
	flex-direction: row;
	z-index: 10;
}

.to-next {
	position: absolute;
	bottom: .25rem;
	left: calc(50% - 1rem);
	display:flex;
	flex: 0 1 auto;
	text-align: center;
	-webkit-animation-name: bounce;
	        animation-name: bounce;
	-webkit-animation-duration: 3s;
	        animation-duration: 3s;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
	-webkit-animation-direction: alternate;
	        animation-direction: alternate;
}

.to-top {
	position: absolute;
	bottom: .25rem;
	left: calc(100% - 3rem);
	display: flex;
	flex: 0 1 auto;
	text-align: right;
	-webkit-animation-name: bounce;
	        animation-name: bounce;
	-webkit-animation-duration: 3s;
	        animation-duration: 3s;
	-webkit-animation-delay: 3s;
	        animation-delay: 3s;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
	-webkit-animation-direction: alternate;
	        animation-direction: alternate;
}

@-webkit-keyframes bounce {
	0% {-webkit-transform: translateY(0);transform: translateY(0)}
	25% {-webkit-transform: translateY(-50%);transform: translateY(-50%)}
	50% {-webkit-transform: translateY(0);transform: translateY(0)}
	75% {-webkit-transform: translateY(-.25%);transform: translateY(-.25%)}
	100% {-webkit-transform: translateY(0);transform: translateY(0)}
}

@keyframes bounce {
	0% {-webkit-transform: translateY(0);transform: translateY(0)}
	25% {-webkit-transform: translateY(-50%);transform: translateY(-50%)}
	50% {-webkit-transform: translateY(0);transform: translateY(0)}
	75% {-webkit-transform: translateY(-.25%);transform: translateY(-.25%)}
	100% {-webkit-transform: translateY(0);transform: translateY(0)}
}
.about-page {
	display: flex;
	flex: 0 1 auto;
	flex-direction: column;
	width: 100%;
	height: 100vh;
	opacity: .75;
	font-color: white;
	vertical-align: middle;
}

.about-wrapper {
	width: 100%;
	background-color: orange;
	display: flex;
	flex: 0 1 auto;
	flex-direction: column;
	color: white;
	padding: 2rem, 0 , 2rem, 0;
	box-shadow: .5rem .5rem 1rem 10px #89f1ff;
}

h1 {
	display: flex;
	flex: 0 1 auto;
	font-size: 2rem;
	padding-left: 1rem;
	padding-bottom: 1rem;
}

p {
	display: flex;
	flex: 0 1 auto;
	padding-left: 1rem;
	padding-bottom: .5rem;
}
